  }),
  components: {
    MenuPluris,
    Schedule,
  },
  methods: {
    callPageCard({ id, index }) {
      let pageTitle;
      this.idPluris = id;
      this.idIndex = index;

      if (id === 1 || id === 3) {
        pageTitle = `menu.items${id}[${index}].title`;
      } else {
        pageTitle = `menu.item${id}`;
      }

      const pageContent = `content.items${id}[${index}].text`;

      this.title = this.$i18n.t(pageTitle);
      this.content = this.$i18n.t(pageContent);
    },
    callInitialPage() {
      this.$router.push({ name: 'home' });
    },
    changePageInside(pageId) {
      const array = this.$i18n.t(`content.items6[0].coord${pageId}`);
      const id = array[0];
      const index = array[1];

      this.callPageCard({ id, index });
    },
  },
  created() {
    this.title = this.$i18n.t('menu.items1[0].title');

    this.callPageCard({ id: 6, index: 0 });
  },
};
</script>

